                                </template>
                            </div>
                            <!-- <div class="conv_base float-right mb-4">
                <div class="conv_base_name float-left w-100">Chat with Chris</div>
                <div class="col-12 float-left p-0 mt-4">
                    <div class="conv_base_desc float-left w-100 mb-4">
