            historyMessageDetails: function (messageHistoryAll) {
                var vm = this;
                var is_first_time = messageHistoryAll.is_first_time;
                if (messageHistoryAll.resultSet !== 'undefined' && messageHistoryAll.resultSet.length > 0) {
                    var searchForMsg = messageHistoryAll.searchForMsg;
                    var lastchatId = messageHistoryAll.lastchatId;
                    var room = messageHistoryAll.room;
                    messageHistoryAll.resultSet.forEach((index, messageHistory) => {
