                                                    <img
                                                        src="https://talygenv.github.io/Vue-UI-Content/images/UC/uc_user_new.jpg">
                                                </div>
                                                <div class="member-detail">
                                                    <p><b>{{ member.useR_NAME }} </b></p>
                                                    <div class="log uc_fav pr-2 icons"
