                                                    </div>
                                                </div>
                                            </div>
                                            <div v-if="store.getUserByGroupId.length == 0">
                                                {{ $t('NoRecordfound') }}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            
                                <div  > </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="accordion-window-error" class="text-center text-danger border p-2 no-record"
