                    No record(s) found
                </div>
            </div>
            <div class="chat-panel chat-content" id="creategroup" v-if="isCreateGroup && topFilterSelect !='Groups'">
            
                <Form id="AddGroup" ref="newGroup" v-slot="{ valid, errors }">
                    <div class="chat-header">
                        <div class="name">
                            {{ $t('CreateChatGroup') }}
                        </div>
                    </div>
                    <div class="form create-group-form">

                        <div class="form-group">
                            <label>{{ $t('GroupChatName') }}</label>
                            <input id="groupTitle" class="form-control" maxlength="30" v-model="Grouptitle">
                            <small class="text-danger" v-if="Grouptitle.length == 30">{{ $t('CharacterLimit') }}</small>
                        </div>
                        <div class="form-group">
                            <label>{{ $t('Members') }}:</label>
                            <multiselect :close-on-select="false" limit="2" v-model="selectedMembers"
