        $t('Update') : $t('Create') }}</a>
                            <a href="javascript:;" id="createGroupCancelBtn" class="btn btn-danger"
                                v-on:click="updategroup ? canceleGroup() : backToChat();">{{
        $t('Cancel') }}</a>
                        </div>
                    </div>
                </Form>
            </div>
        </div>

        <div v-else class="div_forDifferentTabs content chatdv chat-region">
