                                    </div>
                                    
                                    <div class="message-data">
                                        <div class="message-header">
                                            <span class="author-name mr-2">{{ item.USER_NAME }}</span>
                                            <span class="author-name message-time">{{ DateDifference(item.CREATED_AT)
                                                }}</span>
                                        </div>
                                        <a :name="item.MESSAGE_TOKEN" href="javascript:;" class="width_100 hyperLink"
