                                            <div class="row no-margin message-info-row replyBack ml-0" >
                                                <div class="arrow"></div>
                                                <p data-content="username" class="message-sendername w-100">
                                                    {{ item.PARENT_DETAIL == undefined ? item.PARENT_DETAIL :
                                                        item.PARENT_DETAIL.split('~')[1] }}</p>
                                                <div class="messagebody">
                                                    <p data-content="body" class="message-body"
                                                        v-html="item.PARENT_MESSAGE"></p>
                                                </div>
                                            </div>
                                            <div style="clear:both"></div>
                                        </a>
                                        <div class="message-body" v-if="item.MESSAGE != 'attachment' && item.ATTACHMENT!=1">
                                            <p  v-html="item.MESSAGE"></p>
                                           
                                        </div>  
                                        <div class="message-body" v-if="item.MESSAGE == 'attachment' && item.ATTACHMENT==1">
                                            <!-- <p>{{ item.STORAGE_FILE_SIZE }}</p> -->
                                            <a :class="{ 'attachment box p-2 border d-inline-block mb-2': item.FILEEXTENSION == '.jpg' || item.FILEEXTENSION == '.png', 'attachment box mb-2 verticlealignmiddle text-center': item.FILEEXTENSION == '.pdf' }"
                                                
                                                @click="openLightbox(item.FILEPATH,item.FILEEXTENSION,item.STORAGE_FILE_NAME)"
                                                style="width: 88px;height: 88px;vertical-align:middle;margin-bottom: .5rem !important;padding: 8px; font-size: 36px"
                                                :title="item.STORAGE_FILE_NAME">
                                                <img v-if="item.FILEEXTENSION == '.jpg' || item.FILEEXTENSION == '.png'"
                                                @click="openLightbox(item.FILEPATH,item.FILEEXTENSION,item.STORAGE_FILE_NAME)"
                                                    :src="item.FILEPATH"
                                                    loading="lazy" alt="image" style="width:100%;height:70px;">
                                                <i class="m-0 p-0 fa fa-file-pdf-o"
                                                    v-if="item.FILEEXTENSION == '.pdf'"
                                                    style="margin-right:0px;line-height:2em;"></i>
                                        </a>
                                            <!-- <a class="attachment box"   @click="openLightbox(item.FILEPATH,item.FILEEXTENSION,item.STORAGE_FILE_NAME)">
                                                <i class="fa fa-file-image-o"></i> {{ item.STORAGE_FILE_NAME }}
                                            </a> -->
                                        </div>
                                        
                                        <div class="msg-actions">
                                            <ul>
                                                <li><a id="msgReplyOwn" class="noToolTip"
