                                                </li>
                                                <li><a id="msgForwardOwn" class="noToolTip"
                                                        v-on:click="ForwardMessageTo(item)" href="javascript:;"
                                                        title="Forward Message"><i class="fa fa-share"
                                                            aria-hidden="true"></i></a></li>
                                                <li><a id="msgReplyDelete" class="noToolTip"
                                                        v-on:click="DeleteMessage(item, index)" href="javascript:;"
                                                        title="Delete Message"><i class="fa fa-trash"
                                                            aria-hidden="true"></i></a></li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div class="clearfix"></div> 
                                </div>
                              
                                <div class="own-message message-bx myself progressbasediv" id="fileProgressId" v-if="fileInProgressShow" ref="progressDiv" tabindex="0">
                                    <!-- <div class="control-section" style="
                                    float: left;
                                    width: 100%;
                                    text-align: center;
                                "><div><em class="fa fa-upload"></em> Sending file in process...</div>
                                <progress class="bg-success" id="progressBar" max="100" :value="progressValue" style="width: 100%;"></progress></div> -->
                                <div class="control-section" style=" float: left; width: 100%; text-align: center;">
                                    <em class="fa fa-upload"></em> Sending file in process...</div>
                                <div class="progress w-100" >                                    
                                <div class="progress-bar bg-success progress-bar-striped" role="progressbar" :style="'width: '+progressValueShow +'%'" :aria-valuenow="progressValue" aria-valuemin="0" aria-valuemax="100"></div>
                               </div>
                            
                            </div>


                                
                            </div>
                        </div>
                    </div>
                </div>
                


                <div v-if="isShowTyping" class="useristypingbase" :class="{'group': isChatOnGroup==true}">
                    <div class="useristyping2" v-if="isChatOnGroup==false  &&  userGroupId==isTypingGroupId" >{{ isTyping }} <img :src="isTypingImage" /></div>
                    <!-- <div class="useristyping2" v-if="isChatOnGroup==true" ><img v-for="itmImg in isTyping" :src="itmImg" /> <img :src="isTypingImage" /></div> -->

                    <div class="groupistyping" v-if="isChatOnGroup==true &&  userGroupId==isTypingGroupId">
                        <template>
                        <div class="group_dp float-left" v-for="itmImg in isTyping">
                            <img  :src="itmImg == null ? defaultImg: itmImg" />
                        </div>
                        <div class="useristyping2"  >
                            <img :src="isTypingImage" />
                        </div>
                    </template>
                    </div>
                </div>
               
                
                <div class="message-footer" v-if="userGroupId">
                    <div class="chattingarea_outer">
