                                    <div class="user-avatar">
                                        <img :src="rplyonitm.AVATAR != '' ? rplyonitm.AVATAR : defaultImg"
                                            alt="profile">
                                    </div>
                                    <div class="message-data replycopied" :id="rplyonitm.CHATLOG_ID">
                                        <div class="message-header">
                                            <span class="author-name mr-2">{{ rplyonitm.USER_NAME }}</span>
                                            <span class="author-name message-time">{{
                                                    DateDifference(rplyonitm.CREATED_AT)
                                                }}</span>
                                        </div>
                                        <a :name="rplyonitm.MESSAGE_TOKEN" href="javascript:;"
                                            class="width_100 hyperLink">
                                            <div style="clear:both"></div>
                                        </a>
                                        <div class="message-body">
                                            <p v-html="truncateMessage(rplyonitm.MESSAGE)"></p>
                                        </div>
                                    </div>
                                </div>
                                <div class="replycross"><a id="replycross" href="javascript:;"
                                        v-on:click="ReplyCross"><i class="fa fa-times text-danger"></i></a></div>
                            </div>
                            <div id="gifDiv" class="replyhtmlbaseGIF" v-if="isShowGifPanel">

                                <div class="ball-loader" v-if="isDataGifLoaded">
                                    <div class="ball-loader-ball ball1">
                                    </div>
                                    <div class="ball-loader-ball ball2">
                                    </div>
                                    <div class="ball-loader-ball ball3">
                                    </div>
                                </div>
                                <div class="imgGif" v-for="gifItems in GifImageData">
