                                        v-on:click="SetGifToSendArea(gifItems)" :src="gifItems.media[0].gif.url" alt="">
                                    <!-- gifItems.media[0].webp_transparent.preview -->
                                    <a class="gif_cross" v-on:click="RemoveGifToSend()"><i
                                            class="fa fa-close text-danger">
                                        </i>
                                    </a>
                                </div>
                            </div>
                            <div id="div_chatTextbox" class="chattingarea">
