                                            autocorrect="off" autocapitalize="off">
                                            <img data-isgif="1" id="gif0" :src="gifSource" alt=""
                                                style="float:left;margin-right:5px;">
                                            <a class="gif_cross" v-on:click="RemoveGifToSend()">
                                                <i class="fa fa-close text-danger" style="cursor:pointer;">
                                                </i>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                                <div v-if="txtMessage == ''" class="uc_msgsicons">
                                    <a title="Gif" id="gif" href="javascript:;" class="icon_gif noToolTip" v-if="isGif"
                                        v-on:click="ShowGifDiv"><i class="fa fa-file-image-o">
                                        </i>
                                    </a>
                                    <a title="File Sharing" id="attachmentFile" href="javascript:;"
                                        v-on:click="AttachmentFileClick" v-if="isGif" class="icon_clip noToolTip">
                                        <i class="fa fa-paperclip"></i></a>
                                    <a title="Chat Search" id="a_chatSearch" v-if="isShearchForMsg"
                                        v-on:click="SearchFortxt()" href="javascript:;" class="icon_search noToolTip"><i
                                            class="fa fa-search"></i></a>

                                    <a title="GIF Search" id="a_GIFSearch" v-if="searchGIFbyTxt"
                                        v-on:click="SearchForGIF()" href="javascript:;" class="icon_search noToolTip"><i
                                            class="fa fa-search"></i></a>

                                    <a href="javascript:;" id="span_searchDown" v-if="indexArr.length > 1"
                                        v-on:click="searchDownClick"><i class="fa fa-chevron-down mr-2 ml-2"></i></a>
                                    <a href="javascript:;" id="span_searchUp" v-if="indexArr.length > 1"
                                        v-on:click="searchUpClick()"><i class="fa fa-chevron-up mr-2 ml-2"></i></a>
                                    <a href="javascript:;" v-on:click="CloseSearchBox" class="noToolTip"
                                        v-if="isShowCloseSearchIcon" title="Cancel" id="span_closeSearchBox"><i
                                            class="fa fa-times text-danger ml-2"></i></a>
                                </div>

                                <div class="emojibase" v-if="isGif">
                                    <emoji-picker @emoji="append" :search="search">
                                        <button type="button" class="emoji-invoker" slot="emoji-invoker"
                                            slot-scope="{ events: { click: clickEvent } }"
                                            @click.stop="clickEvent($event)">
                                            <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"
                                                class="h-6 w-6 fill-current text-grey">
                                                <path d="M0 0h24v24H0z" fill="none" />
                                                <path
                                                    d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zM12 20c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8zm3.5-9c.83 0 1.5-.67 1.5-1.5S16.33 8 15.5 8 14 8.67 14 9.5s.67 1.5 1.5 1.5zm-7 0c.83 0 1.5-.67 1.5-1.5S9.33 8 8.5 8 7 8.67 7 9.5 7.67 11 8.5 11zm3.5 6.5c2.33 0 4.31-1.46 5.11-3.5H6.89c.8 2.04 2.78 3.5 5.11 3.5z" />
                                            </svg>
                                        </button>
                                        <div slot="emoji-picker" slot-scope="{ emojis, insert, display }">
                                            <div class="emoji-picker"
                                                :style="{ top: display.y + 'px', left: display.x + 'px' }">
                                                <div class="emoji-picker__search">
                                                    <input type="text" v-model="search" v-focus>
                                                </div>
                                                <div>
                                                    <div v-for="(emojiGroup, category) in emojis" :key="category">
                                                        <h5>{{ category }}</h5>
                                                        <div class="emojis">
                                                            <span v-for="(emoji, emojiName) in emojiGroup"
                                                                :key="emojiName" @click="insert(emoji)"
                                                                :title="emojiName">{{ emoji }}</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </emoji-picker>
                                </div>
                                <div class="uc_sendbtn"
                                    v-if="txtMessage != '' || isSearchTxt == true && gifSource != ''"><a
                                        href="javascript:;" v-on:click="sendMessage(chatHistoryDetails)" title="Send"
                                        id="sendmsgbtn" class="noToolTip"><i class="fa fa-paper-plane"></i></a></div>
                            </div>

                            <div class="uc_msgsiconslist">
                                <!-- <a href="javascript:;"><i
                                class="fa fa-smile-o text-info mr-3"></i></a> -->
                            </div>
                        </form>
                    </div>
                </div>

            </div>
            <div class="dev_msgbx_left_for_attachment" v-if="isFileActive">
                <div class="files-tab" data-simplebar="" style="height:80vh; overflow-y:auto">
                    <div class="text-center text-danger border p-2" v-if="ChatFileList.length == 0">
                        {{ $t('NoRecordfound') }}
                    </div>
                    <div class="user card-contact message-both" :id="'chatLog_' + CHATLOG_ID"
                        v-for="(files, filesIndx) in ChatFileList" :key="filesIndx">
                        <i class="fa fa-file-image-o i_attachmentIcon"></i>
                        <div class="designation">{{ files.STORAGE_FILE_NAME }} <span class="sub-c-details">{{
        files.CREATED_AT }}</span></div>
                        <div class="icons">
                            <a id="attachmentForward" href="javascript:;" class="phone" title="Forward"><i
                                    class="fa fa-share"></i></a>
                            <a :href="files.FILEPATH" class="video_do" title="Download"><i
                                    class="fa fa-download"></i></a>
                        </div>
                    </div>
                </div>



            </div>
            <!-- <div class="dev_msgbx_left_for_attachment">
            <div data-simplebar="" class="files-tab"></div>
        </div> -->
            <div id="div_globalforwardData" class="forward-pannel-wrapper px-1 col-3 team_info" style=""
                v-if="isForwardToUser">
                <div id="div_forwardOuter" class="forward-pannel-inner"></div>
                <div class="forwardChatDiv">
                    <div class="frwrd-srch-bar mb-2 mt-1 search-menu">
                        <input type="text" class="inputcrossfix" placeholder="Search" v-on:keyup="SearchUsears($event)"
                            id="search-Forward" v-model="txtSearchForward" aria-label="Username"
                            aria-describedby="basic-addon1" autocomplete="off">
