        notifyMeChat: function(data) {
            $.notifyClose();
            var body = "You have received a new Message (" + data + ")";
            var icon = '';
            var title = Globalize.localize("TALYGEN") + ":" + Globalize.localize("UnifiedCommunication");
            var duration = 10000;
            if (!("Notification" in window)) {
                alert("This browser does not support desktop notification");
            }
            else if (Notification.permission === "granted") {
                displayNotificationChat(body, icon, title, duration, data);
            }
            else if (Notification.permission !== "denied") {
                Notification.requestPermission().then(function (permission) {
                    if (permission === "granted") {
                        displayNotificationChat(body, icon, title, duration, data);
                    }
                });
            }
        },
        displayNotificationChat(body, icon, title, duration, data) {
            var duration = duration || 10000; // Default duration is 5 seconds
            var options = {
                body: body
            };
            var n = new Notification(title, options);
            setTimeout(n.close.bind(n), duration);
        },
        bellDetails : function (count) {
            if (count > 0) {
                $("#popChatCount").show();
                this.notifyMeChat(count);
            }
            if (count == 0) {
                $("#popChatCount").hide();
            }
            if (count > 99) {
                count = 99;
            }
            $('#popChatCount').html(count);
        },
        ShowTransitionOnReply: function(itm)
        {
            var vm=this;
            var parentToken=itm.split('~')[2];
            if (parentToken != '') {
                var scrollTo = 0;
                var Indexr = vm.chatHistoryDetails.resultSet.filter(x => x.MESSAGE_TOKEN == parentToken);
                if (Indexr != null || Indexr != '' || Indexr != undefined) {
                    // document.getElementById('chatLog_' + Indexr[scrollTo].CHATLOG_ID).scrollIntoView();
                    
                    var element = document.getElementById('chatLog_' + Indexr[scrollTo].CHATLOG_ID);
                    element.scrollIntoView({ behavior: 'smooth', block: 'start' });
                    vm.blinkRepliedText = Indexr[scrollTo].CHATLOG_ID;
               
                    setTimeout(function () {
                        vm.blinkRepliedText = '';
                        vm.store.$state.globalSearch = '';
                    }, 8000);
                }
            }
        },
        truncateMessage(message) {
            const maxLength = 200;
            var gifRegex = /(https?:\/\/.*\.(?:gif))/i;

            // Regular expression to match emojis
            var emojiRegex = /[\u{1F600}-\u{1F64F}\u{1F300}-\u{1F5FF}\u{1F680}-\u{1F6FF}\u{2600}-\u{26FF}\u{2700}-\u{27BF}]/u;

            // Check if the text contains a GIF URL
            var hasGif = gifRegex.test(message);

            // Check if the text contains an emoji
            var hasEmoji = emojiRegex.test(message);

            // Check if the message contains an HTML img tag
            if (/<img[^>]+src="([^">]+)"/.test(message) || /[\u{1F600}-\u{1F64F}\u{1F300}-\u{1F5FF}\u{1F680}-\u{1F6FF}\u{2600}-\u{26FF}\u{2700}-\u{27BF}]/u.test(message)) {
                // If it contains an image tag, return the original message and Check if the text contains an emoji
                return message;
            }
            else {
                if (message.length > maxLength) {
                    // If it doesn't contain an image tag, truncate the message
                    return message.substring(0, maxLength) + '...';
                }
            }
            return message;
        },

        checkIsTyping: function (e) {
            
            var vm = this;
            if(vm.txtMessage.trim() !='')
            {
           
            var toUser = vm.ChatUserListData.find(x => x.GROUP_ID == vm.userGroupId);
            if (toUser.IS_GROUP == true) {
                // # Step 1: Split the string by underscore
                var values = toUser.GROUP_NAME_ALIAS.split('_');
                //   # Step 2: Remove duplicates
                var unique_values = [...new Set(values)]
                //   # Step 3: Join unique values with commas
                var commaSaperatedGroupIds = unique_values.join(',');
                vm.$socket.emit('is typing', { user: vm.sender.id, Group: vm.userGroupId, typing: true, IsGroup: true });
            }
            else {
                if (toUser == undefined) {
                    toUser = vm.ChatUserListData.find(x => x.GROUP_ID == vm.userGroupId.toString());
                }
                if (e.which != 13 && vm.txtMessage != '') {
                    vm.$socket.emit('is typing', { user: toUser.RECEIVER_ID, Group: vm.userGroupId, typing: true, IsGroup: false });
                }
                else if (vm.txtMessage == '') {
                    vm.typing = false;
                    vm.$socket.emit('is typing', { user: toUser.RECEIVER_ID, Group: vm.userGroupId, typing: false, IsGroup: false });

                }
            }
          }
