                    id: vm.GetUserInfo.ID,
                    emaiId: vm.GetUserInfo.Email,
                    name: vm.GetUserInfo.Name,
                    imgPath: vm.blobUserUrl + vm.GetUserInfo.Avatar,
                    type: vm.type
                }
                var roomdata =  vm.selectedMembers.map(obj => obj.USER_ID).join(',')
                var params = new Object();
                params = obj;
                params.room = roomdata.replace(/,/g, '_'),
                    params.groupName = vm.Grouptitle,
                    params.alias = roomdata.replace(/,/g, '_')
                vm.$socket.emit('createRoom', params);
                vm.$parent.closeGroup()
                vm.groupShowProps = false
                vm.Grouptitle ='' 
                //for login user select 
                vm.selectedMembers = []
                vm.selectedMembers.push({
            "USER_ID": vm.GetUserInfo.ID,
            "CONTACTNAME":vm.GetUserInfo.Name
                })
                vm.groupId = ''
            } else {
                vm.ShowAlert(vm.$t("Atleast3Members"), "failure", true, vm.$t("Alert"));
            }
            // } else {
            //     vm.ShowAlert(vm.$t("GroupChatName"), "failure", true, vm.$t("Alert"));
            // }
        },
        async getContactRecordBy(event) {
            this.groupShow = true
            this.topFilterSelect = 'Groups'
            this.store.groupList(`emaiId=${this.GetUserInfo.Email}&isGroup=1&room=1`)
            let param = {
                id: this.GetUserInfo.ID,
                emaiId: '',
                companyId: this.GetUserInfo.companyId
            }
            this.$socket.emit('GetAllMyGroup', param, function (record) {
               
            });

        },
        async updateGroup() {
            var vm = this;
            vm.groupShow = true
            let obj = {
                companyId: vm.GetUserInfo.CompanyId,
                id: vm.GetUserInfo.ID,
                emaiId: vm.GetUserInfo.Email,
                name: vm.GetUserInfo.Name,
                imgPath: vm.blobUserUrl + vm.GetUserInfo.Avatar,
                type: vm.type
            }
            var roomdata = vm.selectedMembers.map(obj => obj.USER_ID).join(',')
            var params = new Object();
            params = obj;
            params.userIds = roomdata
            params.groupName = vm.Grouptitle
            let isGroupOrNot = (params.userIds.split("_").length - 1);
            if (isGroupOrNot > 1) {
                isGroupOrNot = 1;
            } else {
                isGroupOrNot = 0;
            }
            params.groupId = vm.groupId
            params.fromSP = 0
            params.GROUP_ID_OUT = 0
            params.isGroupOrNot = isGroupOrNot
            await vm.store.addOrUpdateGroup(params)
            if (vm.store.addUpdateGroupResState[0]) {
                vm.topFilterSelect = 'Groups'
                vm.updategroup = false
                vm.$parent.closeGroup()
                vm.Grouptitle =''
                vm.selectedMembers = []
                vm.selectedMembers.push({
                 "USER_ID": vm.GetUserInfo.ID,
                 "CONTACTNAME":vm.GetUserInfo.Name
                })
                vm.groupId = ''
                vm.store.groupList(`emaiId=${this.GetUserInfo.Email}&isGroup=1&room=1`)
            }

        },
        canceleGroup(){
            this.Grouptitle =''
            this.selectedMembers = []
            this.groupId = ''
            this.$parent.closeGroup()
            this.topFilterSelect = 'Groups'
            this.selectedMembers.push({
            "USER_ID": this.GetUserInfo.ID,
            "CONTACTNAME":this.GetUserInfo.Name
            })
        },
        backToChat(){
            this.groupId = ''
            this.$parent.closeGroup()
            this.topFilterSelect = ''
            this.groupShow = false
            this.updategroup = false
            this.selectedMembers = []
            this.selectedMembers.push({
            "USER_ID": this.GetUserInfo.ID,
            "CONTACTNAME":this.GetUserInfo.Name
        })
        //this.ScrollBottomChat();
        },
        async editGroup(itm) {
            await this.store.userByGroupId(`emaiId=${this.GetUserInfo.Email}&isGroup=1&room=${itm.grouP_ID}`)
            this.$parent.CreateGroupClick()
            this.topFilterSelect = '',
            this.updategroup = true
            this.Grouptitle = itm.grouP_NAME
            this.groupId = Number(itm.grouP_ID)
            this.selectedMembers = []
            this.store.getUserByGroupId.map((record) => {
                this.selectedMembers.push({
                    "USER_ID": Number(record.useR_ID),
                    "CONTACTNAME": record.useR_NAME
                })
            })
        },
        async deleteGroup(id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteGroup"), true, (vm.$t("Delete") + "  " + +vm.$t("Records")), async function () {
                await vm.store.deleteGroup(`emaiId=${vm.GetUserInfo.Email}&groupId=${id}`)
                if (vm.store.getDeleteGroupRes) {
                    vm.ShowAlert(vm.$t("DeleteGroup"), "success", true, vm.$t("Alert"));
                    vm.store.groupList(`emaiId=${vm.GetUserInfo.Email}&isGroup=1&room=1`)
                } else {
                    vm.ShowAlert(vm.$t("Error"), "warning", true, vm.$t("Alert"));
                }
            })
        },
        deleteGroupUser(id, room) {
            var vm = this;
            let obj = {
                room: room,
                DeleteId: id,
                id: vm.GetUserInfo.ID,
                emaiId: vm.GetUserInfo.Email,
            }
            vm.confirmR(vm.$t("ConfirmDeleteGroupUser"), true, (vm.$t("Delete") + "  " + +vm.$t("Records")), async function () {
                DataService.DeleteUserFromGroup(obj).then((record) => {
                    vm.store.$state.userByGroupIdState = vm.store.getUserByGroupId.filter((item) => item.useR_ID !== id)
            
                    if (record.data) {
                        vm.ShowAlert(vm.$t("DeleteUserSuccess"), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t("Error"), "warning", true, vm.$t("Alert"));
                    }
                })

            })

        },
        /***********Group*********/
    },
    setup() {
        const store = communicationStore()
        const store2 = useloggedUserInfo()
        return {
            store,
            store2
        }
    }
}

</script>

